.showcase-content.left.active #close::before{
	font-family: Arial, Helvetica, sans-serif;
	content: 'x';
	position: absolute;
	top: 0%;
	right: 10%;
	font-size: 2em;
	color: #fff;
	width: 70px;
	height: 70px;
	pointer-events: all;
	/* background: red; */
	/* cursor: pointer; */
}


.showcase-content.left.active #close:hover::before{
	color: red;
	cursor: pointer;
	transform: rotate(360deg);
	transition: transform 250ms;
}
