.show-box {
	margin: 0 0 0 0;
	height: 100%; 
	width: 100%; 
}
.show-content {
	margin: 1em auto;
	width: 80%;
}
.chart {
	background: inherit;
	width: 90%;
	margin: 0 auto;
}
.show-title {
	font-size: 1em;
	margin: 0 auto 0 0;
	padding: 0.5em 0;
}
.show-stats {
	display: flex;
	justify-content: space-between;
	font-size: 0.6em;
	padding: 0 0 1.5em 0;
}
.btn-track {
	display: none;
	width: fit-content;
	background: #000;
	color: #fff;
	box-shadow: 0 0 10px white;
	padding: 0 0.5em;
	margin: 0.5em auto;
	font-size: 1.5em;
}
.showcase-content:hover .btn-track {
	display: block;
}
.text-container {
	/* height: 330px; */
	width: 100vw;
	background: #000 !important;
	/* background: url('../../img/to.jpg') no-repeat center center/cover; */
	color: rgb(255,255,255);  
	/* background-color: rgba(0,0,0,0.7); */
	/* background-blend-mode: darken; */
	margin-top: 0px;
	/* clip-path: polygon(0 0, 0 100%, 100% 100%, calc(100% - 5vw) 0); */
	/* clip-path: polygon(25% 0%, 100% 0, 75% 100%, 0 100%); */
	/* clip-path: polygon(0 40%, 100% 0%, 100% calc(100% - 5vw), 0% 100%); */
}
.show-text {
	/* background: #000; */
	max-width: 950px;
	padding: 2em 0;
	margin: 2em auto;
	display: flex;
	justify-content: space-between;
}
.show-text h6 {
	padding: 2em;
}
.show-text p {
	font-size: 0.8em;
	width: 70%;
	margin: auto 0;
}
@media (max-width: 1100px) {
	
.showcase-content:hover .btn-track {
		display: none;
	}
	.showcase-container{
		flex-direction: column;
	}
	.showcase-content.left,
	.showcase-content.side{
		height: 430px;
	}
	.showcase-content.left:hover,
	.showcase-content.side:hover{
		height: 430px;
	}
}
@media (max-width: 576px) {
	
	.showcase-content.left,
	.showcase-content.side{
		width: 90%;
		margin: 0 auto;
		height: 400px;
	}
	
	.showcase-content.side:hover{
		height: 400px;
	}
	.show-stats {
		flex-direction: column;
	}
}
