	margin: 2em auto;
	font-size: 0.7em;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
}

.calc .cur-list {
	position: absolute;
	top: 0;
	right: 0;
}
.res {
	position: relative;
}
.res-list {
	position: absolute;
	top: 0;
	right: 0;
}
.calc .cur-list li,
.res-list li {
	list-style: none;
}
.calc .cur-select {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
}
.calc .res-select {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
}
.calc .cur-select.active {
	display: block;
}
.cur-list button {
	background: #000000;
	color: #fff;
	font-size: 1.3em;
	width: 150px;
	height: 85px;
	position: relative;
}
.res-list button {
	background: red;
	color: #fff;
	font-size: 1.3em;
	width: 150px;
	height: 85px;
	position: relative;
}

.cur-list button::after,
.res-list button::after {
	content: '>';
	position: absolute;
	transform: rotate(90deg);
	color: #fff;
	/* top: 50%; */
	right: 0;
}
.calc label.calc-inp,
.calc label.calc-out {
	background: #fff;
	display: block;
	padding: 10px 0 0px 15px;
	padding: 10px 0 0px 4px;
	margin: 0em auto;
	/* min-height: 30px; */
