/* Reset Select */
select {
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	appearance: none;
	outline: 0;
	box-shadow: none;
	border: 0 !important;
	background: inherit;
	background-image: none;
}
/* Remove IE arrow */
select::-ms-expand {
	display: none;	top: 1px;
}
/* Custom Select */
.select {
	position: relative;
	display: flex;
	width: 100px;
	height: 84.5px;
	line-height: 3;
	background: rgb(255, 255, 255);
	overflow: hidden;
	border-radius: .25em;
	position: absolute;
	top: 0;
	right: 0;
}
@-moz-document url-prefix() {
	.select {
		height: 80px;
	}
}
select {
	flex: 1 1;
	padding: 0 .5em;
	color: rgb(255, 0, 0);
	cursor: pointer;
	font-size: 1em;
	border-color: 3px solid red;
}
/* Arrow */
.select::after {
	content: '\25BC';
	position: absolute;
	top: 0;
	right: 0;
	padding: 0 0em;
	background: inherit;
	color: rgb(255, 0, 0);
	cursor: pointer;
	pointer-events: none;
	transition: .25s all ease;
}
/* Transition */
.select:hover::after {
	color: #f39c12;
}

