/* @import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,700;1,500;1,900&display=swap'); */
*,
*::before,
*::after {
	box-sizing: border-box;
	padding: 0;
	margin: 0;
}
html {
	scroll-behavior: smooth;
}
/* width */
::-webkit-scrollbar {
	width: 7px;
	/* background: transparent; */
	/* background-image: url('./img/herobg.jpg'); */
	background: #fff;
	/* opacity: 0.1; */
}

/* Track */
::-webkit-scrollbar-track {
	/* box-shadow: inset 0 0 5px grey; */
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: red;
	/* background: rgb(0, 0, 0); */
	border-radius: 50%;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
	background: rgba(255, 0, 0, 0.288);
	background: #000;
}

.sweep-head {
	width: 100vw;
	height: 100vh;
	/* background: #ffffffea; */
}

.sweep {
	position: absolute;
	top: 40%;
	left: 40%;
	height: 100%;
	width: 100%;
}
.sweep::after {
	content: 'Loading...';
	position: absolute;
	top: 40%;
	left: 40%;
	height: 100%;
	width: 100%;
}
.sweep-head.sweep--hide {
	opacity: 0;
	display: none;
}

